@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* @import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap'); */

body {
  font-family: "Montserrat", sans-serif;
  max-width: 100% !important;
  overflow-x: hidden;
  /* height: 100%;
  margin: 0; */
}

:root {
  --primary-white: #f7f5f2;
  --primary-green: #1a7e4e;
  --secondary-green: #135f3a;
  --primary-blue: #3491e7;
  --third-green: #c6d1cc;
}

/* ******************* HOME ******************* */

/* -------------- NAV -------------- */

.Nav {
  background-color: #1a7e4e;
  background-color: var(--primary-green);
  padding: 1rem;
  max-width: 100% !important;
  overflow-x: hidden;
}

/* -------------- Hero -------------- */

.hero {
  background-image: url("https://images.pexels.com/photos/305821/pexels-photo-305821.jpeg?cs=srgb&dl=pexels-scott-webb-305821.jpg&fm=jpg");
  padding: 18%;
  /* opacity: 90%; */
  background-repeat: no-repeat;
  background-size: 100%;
  position: relative;
  /* text-align: center; */
  font-family: "Montserrat", sans-serif;
  max-width: 100% !important;
  overflow-x: hidden;
}

.hero h1 {
  font-size: 80px;
}

.hero h3 {
  font-size: 30px;
}

.hero button {
  background-color: #3491e7;
  background-color: var(--primary-blue);
  border: none;
  margin-right: 0.5rem;
  margin-top: 1rem;
}

.fake-col {
  width: 2rem;
}

.real-col {
  margin-left: auto;
  margin-right: auto;
}

/* -------------- About Me -------------- */

.about-me {
  margin-top: 4rem;
  height: 35rem;
  overflow-x: hidden;
  max-width: 100% !important;
}

.aboutme-section {
  background-color: #c6d1cc;
  background-color: var(--third-green);
  border: 3px solid #1a7e4e;
  border: 3px solid var(--primary-green);
  border-radius: 10px;
  padding: 20px;
  height: 30rem;
  margin-left: 6rem;
}
.aboutme-section h2 {
  text-align: center;
}

.card-header {
  background-color: #3491e7;
  background-color: var(--primary-blue);
  color: #f7f5f2;
  color: var(--primary-white);
  border-radius: 30px !important;
}

.aboutme-section button {
  background-color: #3491e7;
  background-color: var(--primary-blue);
  border: none;
}

.Card {
  margin-left: auto;
  margin-right: auto;
  width: 30rem;
  height: 30rem;
}

.img-container {
  text-align: center !important;
  -webkit-align-items: center !important;
          align-items: center !important;
}

.img-container h3 {
  margin-top: 0.8rem;
  font-weight: bold;
}

.img-container p {
  font-weight: bold;
  color: #3491e7;
  color: var(--primary-blue);
}

.about-me img {
  width: 18rem;
  height: 21rem;
  border-radius: 1rem;
}

.fade-in {
  -webkit-transform: translateY(50px);
          transform: translateY(50px);
}

/* about me header */

.about-me-header {
  background-color: #1a7e4e;
  background-color: var(--primary-green);
  padding: 7rem;
}

.about-me-header h1 {
  font-weight: bolder;
  color: #3491e7;
  color: var(--primary-blue);
  font-size: 3rem;
}

.about-me-header h3 {
  color: #f7f5f2;
  color: var(--primary-white);
}

.span2 {
  font-weight: bold;
}

/* about me hero */

.about-me-hero {
  padding: 7rem;
  text-align: center;
  background-color: #1a7e4e;
  background-color: var(--primary-green);
}

.about-me-hero h1 {
  color: #f7f5f2;
  color: var(--primary-white);
}

.about-me-hero h4 {
  font-weight: bold;
}

/* -------------- Products -------------- */
.card-background {
  background: linear-gradient(
    to top,
    #135f3a 20%,
    rgb(255, 255, 255)80%
  );
  background: linear-gradient(
    to top,
    var(--secondary-green) 20%,
    rgb(255, 255, 255)80%
  );
}
.products-container {
  margin-top: 4rem;
}

.cards-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-transform: translateY(50px);
          transform: translateY(50px);
}

@-webkit-keyframes fadeInUp {
  0% {
    opacity: 0;
    -webkit-transform: translateY(50px);
            transform: translateY(50px);
  }
  100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

@keyframes fadeInUp {
  0% {
    opacity: 0;
    -webkit-transform: translateY(50px);
            transform: translateY(50px);
  }
  100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

.fade-in-up {
  -webkit-animation: fadeInUp 0.7s both;
          animation: fadeInUp 0.7s both;
}

.products-container h2 {
  text-align: center;
}

.product-cards {
  background-color: #f7f5f2;
  background-color: var(--primary-white);
  border: 3px solid #1a7e4e;
  border: 3px solid var(--primary-green);
  border-radius: 1rem;
  /* box-shadow: 0.7rem 0.7rem 0.7rem var(--primary-green); */
  margin: 1rem;
  max-width: 16rem;
  min-width: 16rem;
  max-height: 21rem;
  min-height: 21rem;
}

.product-cards h3 {
  color: #135f3a;
  color: var(--secondary-green);
  font-weight: bold;
  padding-top: 10px;
}

.product-cards img {
  width: 100%;
  max-height: 12.5rem;
  min-height: 12.5rem;
  margin-left: auto;
  margin-right: auto;
  border-top-left-radius: 0.8rem;
  border-top-right-radius: 0.8rem;
}

.card-info {
  text-align: center;
}

.Card-footer {
  color: #f7f5f2;
  color: var(--primary-white);
  padding-top: 11px;
  padding-right: 22px;
  background-color: #1a7e4e;
  background-color: var(--primary-green);
  text-align: right;
  height: 43px;
  border-bottom-left-radius: 0.9rem;
  border-bottom-right-radius: 0.9rem;
}
.Card-footer p {
  font-size: 14px;
}

/* -------------- Testimonials -------------- */

.testimonials {
  background-color: #135f3a;
  background-color: var(--secondary-green);
}

.testimonials h2 {
  color: #f7f5f2;
  color: var(--primary-white);
  text-align: center;
  padding-top: 7rem;
}

.testi-card-con {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  margin-left: auto;
  margin-right: auto;
  width: 70rem;
}

.testi-cards {
  background-color: #f7f5f2;
  background-color: var(--primary-white);
  border-radius: 1rem;
  margin-top: 2rem;
  margin-bottom: 6rem;
  text-align: center;
  padding: 2rem;
  width: 20rem;
  margin-left: auto;
  margin-right: auto;
  box-shadow: 0.5rem 0.5rem 0.5rem #1a7e4e;
  box-shadow: 0.5rem 0.5rem 0.5rem var(--primary-green);
}

.testi-cards img {
  width: 5rem;
  height: 6rem;
  border-radius: 30rem;
  margin-bottom: 2rem;
}

/* -------------- Footer -------------- */
.footer {
  background-color: #1a7e4e;
  background-color: var(--primary-green);
  text-align: center;
  /* margin-top: 5rem; */
  padding: 4rem;
}

